@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
html {
  scroll-behavior: smooth; }

body {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-style: normal;
  color: #505478; }

.sub-header {
  background-color: #1F3637;
  height: 50px;
  font-size: 14px;
  font-weight: 300;
  color: var(--bs-white); }
  .sub-header a {
    color: var(--bs-white);
    text-decoration: none; }
  .sub-header li {
    margin: 0px 15px; }
    .sub-header li:last-child {
      margin-right: 0px; }

.tel {
  color: #FFBC27;
  font-weight: bold; }

.header-navbar nav {
  min-height: 90px; }

.header-navbar .nav-item {
  margin: 0px 15px; }
  .header-navbar .nav-item .active {
    font-weight: bold; }
  .header-navbar .nav-item:last-child {
    margin-right: 0px; }

.header-navbar .navbar-dark .navbar-nav .nav-link {
  color: #fff; }

.header-navbar .navbar-brand {
  min-height: 74px; }
  .header-navbar .navbar-brand img {
    max-height: 40px; }

.list-nostyle {
  list-style-type: none;
  padding: 0;
  margin: 0; }

@keyframes slideIn {
  0% {
    transform: translateY(1rem);
    opacity: 0; }
  100% {
    transform: translateY(0rem);
    opacity: 1; }
  0% {
    transform: translateY(1rem);
    opacity: 0; } }

@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: transform;
    -webkit-opacity: 0; }
  100% {
    -webkit-transform: translateY(0);
    -webkit-opacity: 1; }
  0% {
    -webkit-transform: translateY(1rem);
    -webkit-opacity: 0; } }

@media (min-width: 992px) {
  .animate {
    animation-duration: 0.3s;
    -webkit-animation-duration: 0.3s;
    animation-fill-mode: both;
    -webkit-animation-fill-mode: both; } }

.slideIn {
  -webkit-animation-name: slideIn;
  animation-name: slideIn; }

.dropdown-menu {
  border-radius: 2px;
  border: none; }

#hero-area {
  height: 410px;
  width: 100%;
  background-image: url("../images/course-hero.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }

.swiper-container {
  width: 100%;
  height: 100%; }

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center; }
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover; }
